import selfie from '../src/img/undraw_Selfie_re_h9um.svg';
import Card from './Card';
import Sdata from "./Demo"

const Service = () => {
	return (
		<>
			<div className="my-5">
				<h1 className="text-center">our services</h1>
			</div>
			<div className="container-fluid mb-5 ">
				<div className="row">
					<div className="col-10  mx-auto">
						<div className="row gy-4">
                           
                           

                            {
                                Sdata.map((val,index) => {
                                    return <Card
                                    key={index}
                                    img={val.imgsrc}
                                    title={val.title}
                                    />
                                })
                            }
						</div>
                        
					</div>
				</div>
			</div>
		</>
	);
};

export default Service;
