import {useState} from 'react';

const Contact = () => {

    const [value, setValue] = useState({
        fname:"",
        phone:"",
        email:"",
        message:""
    })


    const formsubmit = (e)=>{
        e.preventDefault();
        alert(`${value.fname}.your mobile numer is ${value.phone}.your mighty email is ${value.email} and you fucking message is ${value.message}`);
    }


    const inputEvent = (e)=>{
        
        const { name ,value } =e.target;
        
        setValue((preval)=>{
            return{
                ...preval,
                [name]:value,
            }
        })

        
    }



	return (
		<>
			<div className="my-5">
				<h1 className="text-center">Contact Us</h1>
			</div>
			<div className="container contact-div">
				<div className="row">
					<div className="col-md-6 col-10 mx-auto">
						<form onSubmit={formsubmit}>
							<div class="mb-3">
								<label
									for="exampleFormControlInput1"
									class="form-label"
								>
									Full name
								</label>
								<input
									type="text"
									class="form-control"
									name="fname"
                                    onChange={inputEvent}
                                    id="exampleFormControlInput1"
									placeholder="enter your name"
								/>
							</div>

							<div class="mb-3">
								<label
									for="exampleFormControlInput1"
									class="form-label"
								>
									
                                    phone number
								</label>
								<input
									type="number"
									class="form-control"
									name="phone"
                                    onChange={inputEvent}
                                   
									placeholder="enter your phone number"
								/>
							</div>
                            <div class="mb-3">
								<label
									for="exampleFormControlInput1"
									class="form-label"
								>
									
                                    email 
								</label>
								<input
									type="email"
									class="form-control"
									name="email"
                                    onChange={inputEvent}
                                    id="exampleFormControlInput1"
									placeholder="enter your email"
								/>
							</div>

							<div class="mb-3">
								<label
									for="exampleFormControlTextarea1"
									class="form-label"
								>
									message
								</label>
								<textarea
									class="form-control"
									name="message"
                                    onChange={inputEvent}
                                    id="exampleFormControlTextarea1"
									rows="3"
								></textarea>
							</div>
                            <div class="col-12">
    <button class="btn btn-outline-primary" type="submit">Submit form</button>
  </div>
						</form>
					</div>
				</div>
			</div>
		</>
	);
};

export default Contact;
